#home {
    section {
        margin-bottom: 1em;
    }
    .cards-div-3 {
        display: flex;
        justify-content: space-between;
        .card {
            width: 32%;
            text-align: center;
            background: #fff;
            padding-bottom: 1.5em;
            .img-wrapper::after {
                padding-top: 75%;
            }
            p {
                color: grey;
            }
            // .info-div {
            //     margin: 0 2.5em;
            // }
            .link {
                border: 1px solid var(--green);
                width: 75%;
                margin: 0 auto;
                padding: .5em 1em;
                color: var(--green);
            }
        }
    }
    .why-us-sec {
        background: #fff;
        padding: 1em;
        ul {
            padding: 0;
        }
        p {
            color: grey;
        }
        li {
            list-style: none;
            display: flex;
            // border: 1px solid navy;
            display: flex;
            align-items: center;
            svg {
                margin-right: .15em;
                font-size: 3.2em;
                fill: grey;
            }
        }
        .learn-more {
            * {
                color: var(--green)
            }
        }
    }
    .various-cars-sec {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: .5em;
        .card {
            background: #d8e6dd;
            text-align: center;
            padding: 1em;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            img {
                width: 100%;
            }
            p {
                margin: .15em 0;
                font-weight: 900;
                color: var(--green)
            }
        }
    }
}

.flex-h {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 520px) {
    #home {
        .cards-div-3 {
            flex-direction: column;
            .card {
                width: 100%;
                display: flex;
                .img-wrapper {
                    width: 50px;
                }
            }
        }        
    }

}